.close-btn {
	width: 40px;
	height: 40px;
	background: var(--general-1);
	border: none;
}

.dialog-header {
	height: 60px;
	border-bottom: 1px solid var(--neutral-2);
}

.dialog-actions {
	height: 72px;
	border-top: 1px solid var(--neutral-2);
}

.cancel-btn {
	color: var(--general-4) !important;
	border: 1px solid var(--general-4) !important;
	height: 40px;
	--mat-mdc-button-persistent-ripple-color: transparent !important;
}

input.search-location {
	border: 1px solid var(--general-2);
	height: 40px !important;
	border-radius: 4px;

	&:focus {
		outline: unset !important;
	}

	&::placeholder {
		color: var(--general-2);
	}
}

.w-slider {
	width: 92% !important;
	touch-action: manipulation;
}

div.search-container {
	position: sticky;
	top: 0;
	width: 100%;
	background: var(--basic-1);
	z-index: 99;
}
mat-slider input {
	touch-action: manipulation;
}
